}

.degree-text{
   font-size: 28px;
   background-color: transparent;
}

.ed-card:hover > .pitt-img{
   animation: fadeOut ease .5s forwards;
}
.ed-card:hover > .google-img{
   animation: fadeOut ease .5s forwards;
}
.ed-card:hover > .aws-img{
   animation: fadeOut ease .5s forwards;
}
 
